import titles from './json/titles.json'
import React from 'react'
import empList from './json/empdb'

let employee = empList.get(3)

export const name = (
    <h1 id="name"
        className="header"
        style={{backgroundColor: 'red'}}>
        {titles.name}
    </h1>
)

export const employer = (
    <h1 id="employer"
        className="header"
        style={{backgroundColor: 'yellow'}}>
        {titles.employer}
    </h1>
)

export const emp = (
    <h1 id="employee"
        className="header"
        style={{backgroundColor: 'blue'}}>
        {employee ? employee.name: null}
    </h1>
)


